export default {
	data() {
		return {
			textList: ['财源滚滚', '步步高升', '一夜暴富', '每赌必赢', '逢凶化吉', '业绩长虹', '生意兴隆', '贵人相助'],
			selIdList: [],
			hasToken: false
		}
	},
	onLoad() {

	},
	onShow() {
		this.hasToken = uni.getStorageSync('token') ? true : false
	},
	methods: {
		goMatch() {
			try {
				let wishList = []
				if (this.selIdList.length === 3) {
					this.selIdList.forEach((item) => {
						wishList.push(this.textList[item])
					})
				} else {
					uni.showToast({
						title: "必须求三项",
						icon: 'none'
					})
					return
				}
				console.log(wishList)
				uni.setStorage({
					key: 'wishList',
					data: wishList,
					success: function() {
						uni.navigateTo({
							url: '/pages/birthday/birthday'
						});
					}
				});
			} catch (e) {
				// error
			}
		},
		getUserInfo(info) {
			console.log(info);
			if (info.detail.userInfo) {
			  console.log("点击了同意授权");
			  wx.login({
				success: (res)=>{
					console.log(res)
				  if (res.code) {
					this.$api.post('/api/user/login',{
						code:res.code,
						iv:info.detail.iv,
						avatar_url: info.detail.userInfo.avatarUrl,
						nickname: info.detail.userInfo.nickName,
					}).then((res)=>{
						console.log(res)
						if(res.token){
							this.hasToken=true
						}else{
							return
						}
						uni.setStorageSync('token',res.token)
						uni.setStorageSync('user_id',res.user_id)
						this.goMatch()
						// uni.setStorage({
						//     key: 'user_id',
						//     data: res.user_id,
						//     success:()=>{
						// 		console.log('user_id set success')
						//     }
						// });
					})
				  } else {
					console.log("授权失败");
				  }
				},
			  })
			} else {
			  console.log("点击了拒绝授权");
			  this.back()
			}
		},
		selItem(index) {
			let i = this.selIdList.indexOf(index)
			if (i > -1) {
				this.selIdList.splice(i, 1)
			} else {
				if (this.selIdList.length >= 3) {
					this.selIdList.shift()
					this.selIdList.push(index)
				} else {
					this.selIdList.push(index)
				}
			}
		}
	}
}
